.p {
    width: 30%;
    height: 55vh;
    margin: 10px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser {
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-desc {
    margin: 20px 0px;
}

.p-h3{
    margin-top: 10px;
    color:rgb(243, 242, 242);
}
.iframe-link:hover{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 1;
}

.iframe-link{
    width: 25vw;
    height: 30vh;
    -webkit-transition: .3s ease-in-out opacity, .2s ease-in-out transform;
    -o-transition: .3s ease-in-out opacity, .2s ease-in-out transform;
    transition: .3s ease-in-out opacity, .2s ease-in-out transform;
    opacity: 0.6;
}



